import { h, Component } from 'preact';
import { library, dom } from '@fortawesome/fontawesome-svg-core';
import { faMapMarkerAlt, faCode, faCube, faCogs, faBlog } from '@fortawesome/free-solid-svg-icons';
import { faApple, faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons';

export default class Card extends Component {
  constructor(props) {
    super(props);
    this.state = {
      skills: [
        {
          title: 'Language',
          icon: 'fa fa-code',
          items: [
            { name: 'HTML5 / Pug' },
            { name: 'CSS3 / SCSS' },
            { name: 'JavaScript / TypeScript' },
            { name: 'GraphQL'},
            { name: 'Java' }
          ]
        },
        {
          title: 'Framework, Library',
          icon: 'fa fa-cube',
          items: [
            { name: 'jQuery' },
            { name: 'Bootstrap, Angular Material, DevExtreme' },
            { name: 'React' },
            { name: 'Vue, Vuex, Vue-router' },
            { name: 'Angular, RxJs, NgRx' },
            { name: 'Express.js, NestJS' }
          ],
        },
        {
          title: 'Solution, Service',
          icon: 'fa fa-cogs',
          items: [
            { name: 'Webpack, Parcel' },
            { name: 'Firebase Hosting / Functions' },
            { name: 'PWA' },
            { name: 'AWS Lambda' },
            { name: 'Adobe Experience Manager'}
          ]
        }
      ],
      links: [
        { name: 'Github', icon: 'fab fa-github', href: 'https://github.com/yuugou727/' },
        { name: 'Blog', icon: 'fa fa-blog', href: 'https://yuugou727.github.io/blog/' },
        { name: 'LinkedIn', icon: 'fab fa-linkedin', href: 'https://www.linkedin.com/in/ronniechangtw/' }
      ]
    };
  }

  componentWillMount() {
    library.add(faMapMarkerAlt, faCode, faCube, faCogs, faApple, faGithub, faBlog, faLinkedin);
  }

  componentDidMount() {
    setTimeout(() => {
      this.myCard.style.opacity = 1;
    }, 1000)
    dom.i2svg();
  }
  render() {
    return (
      <div className="card hide" ref={myCard => this.myCard = myCard}>
        <div className="cardTop">
          <div className="avatar"></div>
          <div className="basicInfo">
            <h2>Ronnie Chang</h2>
            <h5><i className="fas fa-map-marker-alt"></i> Taipei, Taiwan</h5>
            <h4>Front-end Web Engineer</h4>
            <h6>Work setup: VSCode on MacOS <i className="fab fa-apple"></i></h6>
          </div>
        </div>
        <div className="skills">
          {this.state.skills.map(type =>
          (<div className="skill" key={type.title}>
            <h3 className="skill-title"><i className={type.icon}></i> {type.title}</h3>
            <ul>
              {type.items.map(v => 
                <li key={v.name}>{v.name}</li>
              )}
            </ul>
          </div>))
          }
        </div>
        <div className="cardButtom">
          <div className="buttons">
            {
              this.state.links.map(v =>
              (<a key={v} className="btn" title={v.name} target="_blank" rel="noopener noreferrer" href={v.href}>
                <i className={v.icon}></i>
              </a>)
              )
            }
          </div>
        </div>
      </div>
    )
  }
}