import { h, Component } from 'preact';
import Card from './Card'
import Particles from "preact-particles";
import { loadFull } from "tsparticles";
import configJson from '../static/particlesjs-config.json';

export default class App extends Component {
  constructor(props) {
    super(props);

    this.particlesInit = this.particlesInit.bind(this);
  }

  async particlesInit(main) {
    await loadFull(main);
  };

  render() {
    return (
      <div className="container">
        <div className="card-container">
          <Card />
        </div>
        <p class="copyright"><small>Ronnie Chang  2023. 
          Built with <a target="_blank" rel="noopener noreferrer" href="https://preactjs.com/">Preact </a> 
          and <a target="_blank"rel="noopener noreferrer"  href="https://github.com/matteobruni/tsparticles/tree/main/components/preact">tsParticles</a>
        </small></p>
        <Particles
          id="tsparticles"
          init={this.particlesInit}
          options={configJson}
        />
      </div>
    );
  }
}